.reviews__item {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 368px;
  padding: 40px;
  background-color: #ffffff;
}
.reviews__item-content {
  display: flex;
  flex-direction: column;
  gap: 40px;
}
.reviews__item-heading {
  display: flex;
  flex-direction: column;
  gap: 12px;
}
.reviews__item-user {
  margin: 0;
  color: #000000;
  font: 600 0.938rem/1.2 Montserrat, arial, sans-serif;
}
.reviews__item-job-title {
  margin: 0;
  color: #565656;
  font: 500 0.75rem/1.25 Montserrat, arial, sans-serif;
}
.reviews__item-text-card {
  margin: 0;
  color: #000000;
  font: 400 1rem/1.375 Montserrat, arial, sans-serif;
}
.reviews__item-link {
  color: #098fcf;
  font: 600 0.813rem/1.23 Montserrat, arial, sans-serif;
  cursor: pointer;
}
.reviews__item-link:hover {
  color: #043c77;
}
.reviews__link-block {
  display: flex;
  justify-content: center;
  padding-right: 10px;
}
.reviews__link {
  width: 258px;
}

.consultation {
  width: 100%;
  background-color: #043c77;
}
.consultation__wrapper {
  max-width: 1150px;
  margin: 0 auto;
  padding: 80px 0.938em;
}
.consultation__heading {
  margin: 0;
  margin-bottom: 28px;
  color: #ffffff;
  font: 700 2.25rem/1.22 Montserrat, arial, sans-serif;
  text-align: center;
}
.consultation__info {
  margin: 0 auto 30px;
  max-width: 50%;
  color: #ffffff;
  font: 400 0.813rem/1.5 Montserrat, arial, sans-serif;
  text-align: center;
}
.consultation__form {
  border: none;
}
.consultation__fieldset {
  display: flex;
  justify-content: center;
  flex-flow: row wrap;
  margin: 0;
  padding: 0;
  border: none;
}
.consultation__label {
  flex: 0 0 316px;
  padding: 0 15px 15px;
}
.consultation__input {
  width: 100%;
  padding: 12px;
  color: #565656;
  font: 400 0.875rem/1.21 Montserrat, arial, sans-serif;
  border: 1px solid #d4d4d4;
}
.consultation__input::placeholder {
  color: #565656;
}
.consultation__btn {
  flex: 0 0 316px;
  padding: 0 15px 15px;
}
.consultation__politics {
  flex: 0 0 100%;
  margin: 0;
  color: #ffffff;
  font: 400 0.75rem/1.5 Montserrat, arial, sans-serif;
  text-align: center;
}
.consultation__link-block {
  border-bottom: 1px solid #ffffff;
}
.consultation__link {
  color: #ffffff;
  text-decoration: none;
}

.footer {
  width: 100%;
}
.footer__up-container {
  background-color: #f9f9f9;
}
.footer__wrapper {
  display: flex;
  justify-content: space-between;
  max-width: 1160px;
  margin: 0 auto;
  padding: 45px 25px 55px;
}
.footer__wrapper_down {
  padding: 15px 25px;
}
.footer__left-block {
  display: flex;
  flex-basis: 796px;
  justify-content: space-between;
  gap: 30px;
  flex-wrap: wrap;
}
.footer__logo-copyright {
  display: flex;
  flex-direction: column;
  gap: 11px;
}
.footer__logo {
  width: 200px;
}
.footer__copyright {
  color: #6c757d;
  font: 400 0.813em/1.5 Montserrat, arial, sans-serif;
}
.footer__links {
  display: flex;
  justify-content: space-between;
  flex-basis: 370px;
  gap: 20px;
  padding-top: 11px;
}
.footer__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
.footer__item-link {
  color: #505050;
  font: 600 0.875rem/1.5 Montserrat, arial, sans-serif;
  text-decoration: none;
}
.footer__right-block {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  padding-top: 6px;
}
.footer__contacts {
  margin: 0;
  color: #000000;
  font: 700 0.875rem/1.5 Montserrat, arial, sans-serif;
}
.footer__tel {
  color: #098fcf;
  font: 700 1.125rem/1.5 Montserrat, arial, sans-serif;
  text-decoration: none;
}
.footer__policy-link {
  color: #000000;
  font: 400 0.813em/1.5 Montserrat, arial, sans-serif;
  text-decoration: none;
}

.main-page {
  width: 100%;
  max-width: 1440px;
  margin: 5.688em auto 0;
}
.main-page__container {
  max-width: 1150px;
  margin: 0 auto;
  padding: 0 0.938em;
}
.main-page__heading-1 {
  margin-bottom: 3em;
}
.main-page__form-quick-order {
  margin-bottom: 6.313em;
}
.main-page__table-price {
  margin-bottom: 6.25em;
}
.main-page__steps-work {
  display: flex;
  flex-direction: column;
  gap: 3em;
  padding: 100px 0;
}
.main-page__steps-work-cards {
  display: flex;
  gap: 1.938em;
}
.main-page__benefits {
  background-color: #f9f9f9;
}
.main-page__benefits-container {
  display: flex;
  flex-direction: column;
  gap: 48px;
  max-width: 1150px;
  margin: 0 auto;
  padding: 81px 0.938em 72px;
}
.main-page__benefits-cards {
  display: grid;
  width: 100%;
  grid-template-rows: repeat(2, 1fr);
  grid-template-columns: repeat(3, 1fr);
  gap: 30px;
}
.main-page__heading-2 {
  margin-bottom: 26px;
}
.main-page__swiper {
  margin-bottom: 49px;
}